.Topbar {
    width: 75%;
    display: grid;
    grid-template-columns: 75fr 25fr;
    grid-column-gap: 30px;
    padding: 10px 0 0 10px;
    margin-bottom: 30px;
    z-index: 1;
    position: fixed;
    top: 0;
    right: 0;
    transition: .5s;
}

.TobarAfterScroll {
    background-color: #fff;
    padding-bottom: 10px;
}

.Topbar > div:last-child {
    display: none;
}

.Topbar .Search {
    width: 100%;
    display: flex;
    align-items: center;
}

.Topbar .Search .appSearch {
    background: none;
    outline: none;
    border: 0;
    font-family: Poppins;
}

.Topbar .Search .appSearch {
    color: #72676e;
}

.Topbar .Notifications {
    position: relative;
    width: min-content;
    height: min-content;
}

.Topbar .Notifications .count {
    position: absolute;
    top: 0;
    right: 10%;
    background-color: #135bcc;
    color: #f2f4f8;
    font-size: 10px;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Topbar .user-info,
.Topbar .user-info .user {
    display: flex;
    align-items: center;
}

.Topbar .user-info {
    width: 100%;
    justify-content: flex-end;
}

.Topbar .user-info .user .userImg {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin: 0 10px;
}

.Topbar .user-info .user p {
    font-family: Quicksand;
}

.Topbar i {
    cursor: pointer;
}

@media only screen and (max-width: 1201px) {
    .Topbar {
        grid-template-columns: 70fr 25fr 5fr;
        width: 100%;
    }

    .Topbar > div:last-child {
        display: flex !important;
        align-items: center !important;
    }

    .Topbar > div:last-child i {
        font-size: 20px;
    }
}

@media only screen and (max-width: 992px) {
    .Topbar {
        grid-template-columns: 55fr 40fr 5fr;
    }
}

@media only screen and (max-width: 600px) {
    .Topbar {
        grid-template-columns: 90fr 10fr;
        grid-column-gap: 10px;
    }
    .Topbar > div:first-child {
        display: none !important;
    }

    .Topbar .la-times {
        display: none !important;
    }
}